.navigation {
	display: block;
	border-left: 1px solid #FEFEFE;
	border-right: 1px solid #FEFEFE;
	border-bottom: 1px solid #ddd;
	width: 100%;
}
.navigation summary {
	height: 50px;
	line-height: 54px;
	text-indent: 25px;
	font-size: 1em;
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0, #FEFEFE), color-stop(1, #FEFEFE));
	cursor: pointer;
}
.navigation summary::-webkit-details-marker {
	display: none;
}
.navigation summary:after{
	content: "+";
	display: inline-block;
	width: 16px;
	height: 16px;
	margin-right: 1.5em;
	font-size: 1.6em;
	/*font-weight: 700;*/
	float: right;
	color: #aaa;
}
.navigation[open]{
	border-bottom: 1px solid transparent;
}
.navigation[open] summary:after{
	content: "-";
}
.navigation ul {
	margin-bottom: 0px;
	padding: 0px 10px;
	overflow: hidden;
	background: #fff;
}
.navigation ul li {
	list-style: none;
	padding: 10px;
	font-size: 12px;
	border-bottom: 1px solid #ddd;
}
.navigation ul li a {
	display: block;
	color: #000000;
	font-size:12px;
	text-indent:10px;
}
.navigation ul li a img {
	width: 30px;
	height: 30px;
}
.navigation ul li a:hover {
	text-decoration: none;
}
