/* scrolltab */
.scrolltab {
	position: relative;
	width: 1200px;
	margin: 0 auto;
}
.scrolltab .dSmallPicBox {
	width: 688px;
	height: 120px;
	position: relative;
}
.scrolltab .dSmallPic {
	width: 590px;
	height: 120px;
	position: absolute;
	left: 53px;
	top: 0px;
	overflow: hidden;
}
.scrolltab .dSmallPic ul {
	position: absolute;
}
.scrolltab .dSmallPic li {
	width: 141px;
	height: 110px;
	border: 1px solid #fff;
	padding: 3px 2px 0;
	float: left;
	display: inline;
	cursor: pointer;
	overflow: hidden;
}
.scrolltab .dSmallPic .sPic {
	width: 141px;
	height: 106px;
	display: block;
}
.scrolltab .dSmallPic .sPic img {
	border: 1px solid #ddd;
	padding: 2px;
}
.scrolltab .dSmallPic .sTitle {
	width: 141px;
	height: 30px;
	line-height: 30px;
	display: block;
	text-align: center;
	color: #333;
	overflow: hidden;
}
.scrolltab .dSmallPic .liSelected {
	border-color: #e9c184;
}
.scrolltab .dSmallPic .liSelected .sPic img {
	border-color: #fff;
}
