.tab_content_li:before,.tab_content_li:after{
	position: absolute;
	display: block;
	content: "";
	-webkit-transition: all .1s;
	/*transition: all 0.3s;*/
}
.tab_content_li:before{
	top:-1px;left:-1px;
	width:0;height:0;
	border-top:1px solid #F60;
	-webkit-transition-delay: 0.3s;
	/*transition: 0.3s;*/
}
.tab_content_li:after{
	bottom:-1px;right:-1px;
	width:0;height:0;
	border-top:1px solid #F60;
	-webkit-transition-delay: 0.1s;
}
.tab_content_li a:before,.tab_content_li a:after{
	display: block;
	position: absolute;
	content: "";
	-webkit-transition: all 0.1s;
	/*transition: 0.3s;*/
}
.tab_content_li a:before{
	left:-1px;bottom:-1px;
	width:0;height:0;
	border-right:1px solid #F60;
	-webkit-transition-delay: 0s;
	/*transition: 0.3s;*/
}
.tab_content_li a:after{
	right:-1px;top:-1px;
	width:0;height:0;
	border-right:1px solid #F60;
	-webkit-transition-delay: 0.2s;
	/*transition: 0.3s;*/
}
.tab_content_li:hover:before,.tab_content_li:hover:after{width: 101%;
}
.tab_content_li:hover a:before,.tab_content_li:hover a:after{height: 100px;
}
.tab_content_li:hover:before{-webkit-transition-delay: 0.2s;
}/* transition-delay: 0.1s;*/
.tab_content_li:hover:after{-webkit-transition-delay: 0.5s;
}/* transition-delay: 0.7s;*/
.tab_content_li:hover a:before{-webkit-transition-delay: 0.7s;
}/* transition-delay: 1s;*/
.tab_content_li:hover a:after{-webkit-transition-delay: 0.3s;
}/* transition-delay: 0.4s;*//**/
